document.addEventListener("DOMContentLoaded", () => {
    const $bigBall = document.querySelector(".cursor__ball--big");
    const $smallBall = document.querySelector(".cursor__ball--small");
    const $links = document.querySelectorAll("a, button"); // Select all <a> tags

    // Lenis smooth scroll setup
    const lenis = new Lenis({
        duration: 1.5,  // Slightly longer duration for smoother scrolling
        easing: (t) => Math.min(1, t * (2 - t)), // Easing for smooth scrolling
        smoothWheel: true,  // Enable smooth scrolling on wheel events
        direction: "vertical",
        gestureDirection: "vertical",
        smooth: true,
        mouseMultiplier: 0.5,  // Slightly lower multiplier for smoother movement
        smoothTouch: false,
        touchMultiplier: 2,
        infinite: false,
    });

    // Make sure to initialize Lenis scroll frame
    function raf(time) {
        lenis.raf(time);
        requestAnimationFrame(raf);
    }

    requestAnimationFrame(raf);

    // Add mouse move event listener
    document.body.addEventListener("mousemove", onMouseMove);

    // Add hover event listeners to all <a> tags
    for (let i = 0; i < $links.length; i++) {
        $links[i].addEventListener("mouseenter", onMouseHover);
        $links[i].addEventListener("mouseleave", onMouseHoverOut);
    }

    let lastX = 0, lastY = 0;
    // Adjust for smooth scrolling and move the cursor
    function onMouseMove(e) {
        const scrollY = lenis.scroll; // Get scroll position using Lenis

        // Only update if the position has changed (throttle updates)
        if (e.clientX !== lastX || e.clientY !== lastY) {
            lastX = e.clientX;
            lastY = e.clientY;

            const cursorX = e.clientX;
            const cursorY = e.clientY + scrollY; // Add scroll offset to the Y position

            // Use requestAnimationFrame to ensure smooth animation
            requestAnimationFrame(() => {
                TweenMax.to($bigBall, 0.4, {
                    x: cursorX - 15,
                    y: cursorY - 15 // Adjust Y position with scrollY
                });
                TweenMax.to($smallBall, 0.1, {
                    x: cursorX - 5,
                    y: cursorY - 7 // Adjust Y position with scrollY
                });
            });
        }
    }

    // Hover effect to enlarge cursor ball
    function onMouseHover() {
        TweenMax.to($bigBall, 0.3, {
            scale: 4
        });
    }

    function onMouseHoverOut() {
        TweenMax.to($bigBall, 0.3, {
            scale: 1
        });
    }
});
